.Contacts {
    background-color: white;
    width: 100%;
    height: 352px;
    display: flex;
    flex-direction: row;
    align-items: center;
    color:#333333;
    border-radius: 20px;
    .img {
        width: 410px;
        height: 272px;
        margin-right: 50px;
        margin-left: 90px;
    }
    .info {
        h1 {
            font-weight: 700;
            font-size: 30px;
        }
        p {
            font-weight: 400;
            font-size: 15px;
            width: 594px;
        }
    }
}
